<template>
  <div style="margin-top: 10px" v-loading="fullloading" id="base">

    <div style="margin-top: 20px">
      <el-table :data="roleList" size="mini" border stripe @selection-change="handleSelectionChange" v-loading="tableLoading" style="width: 100%">
        <el-table-column type="selection" align="left">
        </el-table-column>
        <el-table-column prop="nameZh" align="center" label="角色">
        </el-table-column>

        <el-table-column label="操作" align="center" width="400"  fixed="right">
          <template slot-scope="scope">
            <el-button v-if="$hasButton('roleMenuEdit')" @click="setAuthority(scope.row.id,{title:'菜单权限'})" type="primary" style="padding: 3px 4px 3px 4px;margin: 10px"
                       size="mini">菜单权限
            </el-button>
            <el-button v-if="$hasButton('roleButtonEdit')" @click="setButtonAuthority(scope.row.id,{title:'按钮权限'})" type="success" style="padding: 3px 4px 3px 4px;margin: 10px"
                       size="mini">按钮权限
            </el-button>
            <el-button v-if="$hasButton('roleEdit')" @click="update(scope.row,{title:'修改'})" type="primary" style="padding: 3px 4px 3px 4px;margin: 10px"
                       size="mini">修改
            </el-button>
            <el-button v-if="$hasButton('roleDel')" @click="deleteMsg(scope.row.id)" type="danger" style="padding: 3px 4px 3px 4px;margin: 10px"
                       size="mini">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="clearfix">
      <div class="fl" style="margin-top: 20px">
        <el-button size="mini" v-if="$hasButton('roleAdd')" @click="add" type="success" icon="el-icon-search">新增</el-button>
        <el-button size="mini" v-if="$hasButton('roleDel')" @click="deleteAll()" type="danger" icon="el-icon-delete">删除</el-button>
      </div>
    </div>

    <!--新增或修改弹框-->
    <el-form style="margin: 0px;padding: 0px;">
      <div style="text-align: left" id="Bdialog">
        <el-dialog
          style="padding: 0px;"
          :close-on-click-modal="false"
          :visible.sync="dialogVisible"
          width="50%">
          <div class="dialog_B Detail">
            <div class="" style="width: 700px;margin: auto">
              <h1 class="H1">{{dialogTitle}}</h1>
              <el-form style="margin: 0px;padding: 0px;">
                <el-form-item label="角色英文名：" prop="name" :label-width="labelWidth">
                  <el-input placeholder="请输入角色英文名" prefix-icon="el-icon-edit" v-model="role.name">
                    <template slot="prepend">ROLE_</template>
                  </el-input>
                </el-form-item>
                <el-form-item label="角色中文名：" prop="username" :label-width="labelWidth">
                  <el-input placeholder="请输入角色中文名" prefix-icon="el-icon-edit" v-model="role.nameZh"></el-input>
                </el-form-item>
              </el-form>
              <span slot="footer" class="dialog-footer" style="text-align: right;margin-top: 20px;width: 100%;display: block">
                <el-button size="small" @click="cancelEidt()">取 消</el-button>
                <el-button size="small" type="primary" @click="sendMsg()">确 定</el-button>
              </span>
            </div>
          </div>
        </el-dialog>
      </div>
    </el-form>

    <!--菜单权限设置弹框-->
    <el-form style="width: 100px">
      <div style="text-align: left">
        <el-dialog
          :title="dialogTitle"
          style="padding: 50px;"
          :close-on-click-modal="false"
          :visible.sync="authorityVisible"
          width="25%">
          <div style="text-align: center">
            <el-tree :props="props"
                     :key="role.id"
                     :data="treeData"
                     :default-expanded-keys="checkedKeys"
                     :default-checked-keys="checkedKeys"
                     node-key="id"
                     ref="tree"
                     show-checkbox
                     highlight-current
                     @check-change="handleCheckChange">
            </el-tree>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button size="mini" @click="cancelEidt">取消修改</el-button>
            <el-button type="primary" size="mini" @click="updateRoleMenu">确认修改</el-button>
          </span>
        </el-dialog>
      </div>
    </el-form>

    <!--按钮权限设置弹框-->
    <el-form style="width: 100px">
      <div style="text-align: left">
        <el-dialog
          :title="dialogTitle"
          style="padding: 50px;"
          :close-on-click-modal="false"
          :visible.sync="buttonauthorityVisible"
          width="50%">
          <div class="dialog_B Detail">
            <div class="" style="width: 700px;margin: auto">
              <el-table :data="menuButtonList" size="mini" border stripe style="width: 100%">
                <el-table-column prop="menuName" align="center" label="所属菜单">
                </el-table-column>
                <el-table-column align="center" label="按钮名称">
                  <template slot-scope="scope">
                    <el-checkbox-group v-model="buttonIds" @change="handleCheckedButtonChange">
                      <el-checkbox v-for="button in scope.row.buttons" :label="button.id" :key="button.id">{{button.buttonName}}</el-checkbox>
                    </el-checkbox-group>
                  </template>
                </el-table-column>
              </el-table>
              <span slot="footer" class="dialog-footer" style="text-align: right;margin-top: 20px;width: 100%;display: block">
                <el-button size="small" @click="cancelEidt()">取 消</el-button>
                <el-button size="small" type="primary" @click="updateRoleButton">确 定</el-button>
              </span>
            </div>
          </div>
        </el-dialog>
      </div>
    </el-form>

  </div>
</template>
<script>
  export default{
    data(){
      return {
        labelWidth:'100px',
        tableLoading: false,
        fullloading: false,
        dialogTitle: '',//表单标题
        dialogVisible:false,//修改的表单的弹框
        authorityVisible:false,//菜单权限弹框
        buttonauthorityVisible:false,//按钮权限弹框
        addbool: false,
        keywords: '',
        roleList:[],
        role:{
          id: null,
          name:"",//角色英文名,
          nameZh:"",//角色中文名,
        },
        idstr:'',
        props: {
          label: 'menuName',
          children: 'children'
        },
        treeData: [],
        checkedKeys: [],

        menuButtonList:[],
        buttonIds:[],
        button:{
          id: null,
          buttonName:"",//按钮名称,
        },
        total: -1,  //总页数
        page: 1,  //当前页
        size:6,   //每页多少条
      }
    },
    mounted: function () {
      this.getList();
    },
    methods: {
      //获取列表
      getList(){
        let _this = this;
        this.tableLoading = true;
        this.$http.get("/system/role/getAll").then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status == 200) {
            if(resp.data.status == 1){//成功
              this.authorityVisible = false;
              this.buttonauthorityVisible = false;
              _this.roleList = resp.data.data; //列表数据
            }else{//失败
              this.$message.error("查询失败");
            }
          }
        }).catch((error=>{
          if (error.response.status == '403') {
            this.$router.push({path: '/forbidden403'}); // 对象
          }
        }))
      },

      //添加信息的预处理
      add(){
        this.role = {};
        this.addbool = true;
        this.dialogVisible = true;
        this.dialogTitle = "新增";
      },
      //修改信息的预处理
      update(val,obj){
        this.dialogVisible = true;//弹框
        this.addbool = false;
        this.dialogTitle = obj.title;
        this.role = window.clone(val);//克隆
        let name = this.role.name;
        name = name.startsWith("ROLE_") ? name.slice(5) : name;
        this.role.name = name;
      },

      searchClick(){
        alert("ff")
      },
      //选择框
      handleSelectionChange(val) {
        var idstr = "";
        for(var　i in val ){
          idstr+=val[i].id+","
        }
        this.idstr = idstr;
      },
      handleCheckedButtonChange(value) {
        console.log(this.buttonIds.toString())
      },

      //取消调用弹框的方法
      cancelEidt(){
        this.dialogVisible = false;
        this.authorityVisible = false;
        this.buttonauthorityVisible = false;
      },
      sendMsg(){
        if(this.addbool){
          this.postMsg();
        }else{
          this.putMsg();
        }
      },

      //新增信息   post请求
      postMsg(){
        let params = this.role;
        this.$http({
          method:"post",
          url:"/system/role/save",
          data:params
        }).then((response)=> {
          let data = response.data;
          if(data.status == 1){ //添加成功
            this.dialogVisible = false;
            this.$message({
              message:'新增成功',
              type:'success'
            });
            this.getList();
          }else {
            data.msg!=null?this.$message.error(data.msg):this.$message.error('新增失败');
          }
        }).catch((error)=>{
          console.log(error);
        })
      },
      //修改信息   put请求
      putMsg(){
        let params = this.role;
        this.$http({
          method:"put",
          url:"/system/role/update",
          data:params
        }).then((response)=> {
          let data = response.data;
          if(data.status == 1){ //添加成功
            this.dialogVisible = false;
            this.$message({
              message:'修改成功',
              type:'success'
            });
            this.getList();
          }else {
            data.msg!=null?this.$message.error(data.msg):this.$message.error('修改失败');
          }
        }).catch((error)=>{
          console.log(error);
        })
      },
      //删除数据  delete请求
      deleteMsg(val){
        let _this = this;
        this.$confirm('确定删除此操作?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          _this.$http({
            method:"delete",
            url:"/system/role/delete/"+val
          }).then((response)=>{
            let data = response.data;
            if(data.status == 1){ //删除成功
              this.$message({
                message:'删除成功',
                type:'success'
              });
              this.getList();
            }else {
              data.msg!=null?this.$message.error(data.msg):this.$message.error('删除失败');
            }
          }).catch((error)=>{
            console.log(error);
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      deleteAll(){
        let ids = this.idstr;
        if(!ids){
          this.$message.error("提示，您还没有选择任何信息");
          return false;
        }else{
          this.deleteMsg(ids);
        }
      },

      //设置菜单权限加载菜单
      setAuthority(val,obj){
        this.loadMenu(val);
        this.authorityVisible = true;//弹框
        this.dialogTitle = obj.title;
        this.role.id = val;
      },

      setButtonAuthority(val,obj){
        this.buttonauthorityVisible = true;//弹框
        this.dialogTitle = obj.title;
        this.role.id = val;
        this.loadButton(val);
      },

      //加载菜单
      loadMenu(val){
        let _this = this;
        this.$http({
          method:'get',
          url:'/system/menu/tree/'+val
        }).then((response=>{
          let data = response.data;
          if (data.status == 1) {
            _this.treeData = data.data.menus;
            _this.checkedKeys = data.data.mids;
          }
        })).catch((error=>{
        }));
      },

      //加载菜单下的按钮
      loadButton(val){
        if (this.menuButtonList.length === 0){
          this.$http({
            method:'get',
            url:'/system/menu/getMenuButtons'
          }).then((response=>{
            let data = response.data;
            if (data.status == 1) {
              _this.menuButtonList = data.data;
            }
          })).catch((error=>{
          }));
        }

        let _this = this;
        this.$http({
          method:'get',
          url:'/system/menu/getButtonIdByRoleId/'+val
        }).then((response=>{
          let data = response.data;
          if (data.status == 1) {
            _this.buttonIds = data.data;
          }
        })).catch((error=>{
        }));
      },

      updateRoleMenu(){
        let params = this.$qs.stringify({
          roleId:this.role.id,
          menuIds:this.$refs.tree.getCheckedKeys(true).toString()
        });
        let _this = this;
        this.$http({
          method:'put',
          url:'/system/role/updateRoleMenu',
          data:params
        }).then((response)=>{
          let data = response.data;
          if(data.status == 1){
            this.$message({
              message:'修改成功',
              type:'success'
            });
            this.getList();
          }else {
            data.msg!=null?this.$message.error(data.msg):this.$message.error('修改失败');
          }
        }).catch((error)=>{
        });
      },

      updateRoleButton(){
        let params = this.$qs.stringify({
          roleId:this.role.id,
          buttonIds:this.buttonIds.toString()
        });
        let _this = this;
        this.$http({
          method:'put',
          url:'/system/role/updateRoleButton',
          data:params
        }).then((response)=>{
          let data = response.data;
          if(data.status == 1){
            this.$message({
              message:'修改成功',
              type:'success'
            });
            this.getList();
          }else {
            data.msg!=null?this.$message.error(data.msg):this.$message.error('修改失败');
          }
        }).catch((error)=>{
        });
      },

      handleCheckChange(data, checked, indeterminate) {
        //console.log(data, checked, indeterminate);
      },

    }
  }
</script>
<style lang="less">
  #base{
    .el-upload-list--picture-card .el-upload-list__item{
      width: 80px;
      height: 80px;
    }
    .el-upload--picture-card{
      width: 80px;height: 80px;
      line-height: 86px;
    }
    .el-upload-list--picture-card{
      float: left;
    }
    .el-form-item__label{
      color: #333333;
      font-weight: bold;
      padding: 2px;
    }

    .el-input{
      width: 80%;
    }
    .el-dialog{
      margin-top: 5vh !important;
      min-width: 500px;
    }
  }

  #Bdialog{
    .H1{
      font: normal normal bold 28px/28px "Adobe 黑体 Std R";
      color: #333333;
      margin-bottom: 30px;
      margin-top: 5px;
    }
    .leader{
      text-align: left;
      margin: 20px 0 ;
      color: @warning;
      .l_bar{
        width: 7px;
        font-size: 18px;
        float: left;
        line-height: 18px;
        height: 21px;
        display: inline-block;
        background: @success;
        margin: 3px 3px 0 0;
      }
    }
    .dialog_B{
      max-height: 700px;
      padding-bottom: 30px;
      overflow-y: scroll;
    }
    .div_main{
      width: 700px;
      margin: auto;
      padding-bottom: 50px;
    }
    .Detail{
      .el-form-item__label{
        font-size: 16px;
        color: #333333;
      }
      .lb_v{
        font-size: 16px;
      }
    }
    .dialog_B::-webkit-scrollbar{
      display:none;
    }
    .el-form-item__content{
      text-align: left;
    }
    .el-dialog__body{
      padding: 6px 20px;
    }
  }

</style>



